import React, {Component} from "react";
import { Container,Row,Box,Image, Col, Column, Button} from '../../styles/init';
import Navigation from '../../reuseables/navigation';
import Footer from '../../reuseables/footer';
import {connect} from 'react-redux';

class CarDetail extends Component {

    mailTo = mail => {
        window.location.href= `mailto:${mail}`
    }

    render() {
       const car = this.props.payload.cars[this.props.match.params.id];
        return (
            <Container>
                <Navigation />
                <Column height="700px" padding="0px 86px">
                <Row justify="flex-start" bottomspacing="20px" padding="0 10px">
                    <img src={require(`../../assets/arrow_right.svg`)} alt="arrow_right"/>
                    <p>Back to results</p>
                </Row>
                <Row bottomspacing="40px">
                    <Col  width="60%">
                        <p>Ford Fiesta Zetec 1.0T EcoBoost 100PS with Start/Stop 6 Speed 5dr</p>
                    </Col>
                    <Col width="40%">
                        <h1 className="price">${car.price}</h1>
                    </Col>                 
                </Row>
                <Row justify="space-between" align="flex-start">
                    <Col width="55%">
                        <Box height="400px" width="100%">

                        <Image src={require(`../../assets/valuation.svg`)} alt="place holder" />

                        </Box>
                    </Col>
                    <Col width="40%" padding="20px 0" >
                        <Row bottomspacing="20px">
                            <h2>About the seller</h2>
                        </Row>
                        <Row bottomspacing="20px">
                            <Col width="20%">
                                <img  className="icon-small" src={require(`../../assets/user-blue.svg`)} />
                            </Col>
                            <Col width="80%">
                                <Column>
                                    <Row bottomspacing="5px"><p className="attr-title">NAME</p></Row>
                                    <Row><p className="attr-val">{car.fullname}</p></Row>
                                </Column>
                            </Col>
                        </Row>
                        <Row bottomspacing="20px">
                            <Col width="20%">
                                <img  className="icon-small" src={require(`../../assets/phone.svg`)} />
                            </Col>
                            <Col width="80%">
                                <Column>
                                    <Row bottomspacing="5px"><p className="attr-title">MOBILE</p></Row>
                                    <Row><p className="attr-val">{car.phone}</p></Row>
                                </Column>
                            </Col>
                        </Row>
                        <Row bottomspacing="60px">
                            <Col width="20%">
                                <img  className="icon-small" src={require(`../../assets/location.svg`)} />
                            </Col>
                            <Col width="80%">
                                <Column>
                                    <Row bottomspacing="5px"><p className="attr-title">LOCATION</p></Row>
                                    <Row><p className="attr-val">{car.city}</p></Row>
                                </Column>
                            </Col>
                        </Row>                       
                        <Row justify="center">
                            <Button width="200px" radius="50px" font="15px" height="40px" className="pulse" onClick={() => this.mailTo(car.email)}>Email seller</Button>
                        </Row>
                    </Col>
                
                </Row>
                <Row>
                    <h1>OVERVIEW</h1>
                </Row>
                    
                </Column>
                
                <Footer />

            </Container>
        )
    }
}

const mapStateToProps = state => {
    return {
        payload: state.Viewallcars.cars_payload
    }
}

export default connect(mapStateToProps)(CarDetail);